.FS__confirmed {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.FS__confirmed__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.FS__confirmed__content h1 {
  font-size: 2.5rem;
  font-weight: 700;
  font-family: var(--font-family);
  text-transform: uppercase;
  margin-bottom: 1rem;
}

.FS__confirmed__content iframe {
  width: 100%;
  height: 425px;
  max-width: 60%;
  max-height: 70%;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, 0.5);
}

.FS__confirmed__content p {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 400;
  font-family: var(--font-family);
  text-align: center;
  margin-bottom: 1rem;
  color: white;
  max-width: 70%;
  margin-top: 2rem;
}

@media screen and (max-width: 2560px) and (min-width: 1500px) {
  .FS__confirmed__content {
    gap: 4rem;
  }
  .FS__confirmed__content h1 {
    font-size: 4rem;
  }
  .FS__confirmed__content iframe {
    height: 825px;
  }
  .FS__confirmed__content p {
    font-size: 2.5rem;
    line-height: 3.5rem;
  }
}

@media screen and (max-width: 1050px) {
    .FS__confirmed__content {
        gap: 2rem;
    }
    .FS__confirmed__content h1 {
        font-size: 2rem;
        text-align: center;
    }
    .FS__confirmed__content iframe {
        height: 275px;
        max-width: 75%;
    }
    .FS__confirmed__content p {
        font-size: 1rem;
        line-height: 1.5rem;
        max-width: 90%;
    }
}

@media screen and (max-width: 650px) {
    .FS__confirmed__content iframe {
        height: 250px;
        max-width: 100%;
    }
}

@media screen and (max-width: 490px) {
    .FS__confirmed__content h1 {
        font-size: 1.5rem;
        text-align: center;
    }
    .FS__confirmed__content p {
        font-size: 0.9rem;
        line-height: 1.2rem;
    }
    .FS__confirmed__content iframe {
        height: 200px;
        max-width: 95%;
    }
}
