.FS_service {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  background: var(--color-footer);
  margin-top: 7rem;

  /* ff 3.6+ */
  background: -moz-radial-gradient(
    circle at 30% -100%,
    #042c54d7 25%,
    rgba(4, 44, 84, 0.1) 85%,
    rgba(27, 120, 222, 0.1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(
    circle at 30% -100%,
    #042c54d7 25%,
    rgba(4, 44, 84, 0.1) 85%,
    rgba(27, 120, 222, 0.1) 100%
  );

  /* opera 11.10+ */
  background: -o-radial-gradient(
    circle at 30% -100%,
    #042c54d7 25%,
    rgba(4, 44, 84, 0.1) 85%,
    rgba(27, 120, 222, 0.1) 100%
  );

  /* ie 10+ */
  background: -ms-radial-gradient(
    circle at 30% -100%,
    #042c54d7 25%,
    rgba(4, 44, 84, 0.1) 85%,
    rgba(27, 120, 222, 0.1) 100%
  );

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 30% -100%,
    #042c54d7 25%,
    rgba(4, 44, 84, 0.1) 85%,
    rgba(27, 120, 222, 0.1) 100%
  );

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.FS_service-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0 2rem;
}

.FS_service-heading h1 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  max-width: 510px;
  font-family: var(--font-family);
}

.FS_service-heading p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-subtext);
  cursor: pointer;
}

.FS_service-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;

  margin-top: 2rem;
}

/* Customizing feature component as per needs */
.FS_service-container .FS__features-container__feature {
  flex: 1;
  margin: 1rem;
  min-width: 210px;
  display: unset;
  flex-direction: column;
}

/* Customizing feature component as per needs */
.FS_service-container .FS__features-container_feature-text {
  margin-top: 0.5rem;
}

@media screen and (max-width: 2560px) and (min-width: 1450px) {
  .FS_service-heading h1 {
    font-size: 42px;
    line-height: 44px;
    padding-left: 20px;
  }

  .FS_service-heading p {
    font-size: 36px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 850px) {
  .FS_service-heading {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0rem;
  }

  .FS_service-heading p {
    margin-top: 1rem;
  }
}

@media screen and (max-width: 650px) {
  /* Customizing feature component as per needs */
  .FS_service-feature .FS__features-container__feature {
    flex-direction: column;
  }

  /* Customizing feature component as per needs */
  .FS_service-feature .FS__features-container_feature-text {
    margin-top: 0.5rem;
  }

  .FS_service-heading h1 {
    font-size: 24px;
    line-height: 40px;
  }

  .FS_service-heading p {
    font-size: 14px;
  }
}

@media screen and (max-width: 350px) {
  /* Customizing feature component as per needs */
  .FS_service-container .FS__features-container__feature {
    margin: 1rem 0;
    min-width: 100%;
  }
}
