.FS__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.5rem;
  padding: 2rem 6rem;
}

.FS__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.FS__navbar-links_logo {
  margin-right: 1rem;
}

.FS__navbar-links_logo-full {
  display: flex;
  align-items: center;
  gap: 10px;
}

.FS__navbar-links_logo-fs {
  width: 50px;
  height: 50px;
  background-color: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  font-size: 17px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 600;
}

.align
{
  display: flex;
  flex-direction: column;
}
.FS__navbar-links_logo-funnel-syndicate {
  color: white;
  font-size: 17px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: 500;
  margin-left: 5px;
}
.FS__navbar-links_logo-return {
  color: white;
  font-size: 12px;
  margin-top: 5px;
  font-family: var(--font-family);
  margin-left: 5px;
  cursor: pointer;
}

.FS__navbar-links_logo-return a {
  color: white;
  text-decoration: none;
}

@media screen and (max-width: 2560px) and (min-width: 1500px) {
  .FS__navbar {
    margin-top: 1.5rem;
  }
  .FS__navbar-links_logo-fs {
    width: 75px;
    height: 75px;
    font-size: 28px;
  }
  .FS__navbar-links_logo-funnel-syndicate {
    font-size: 28px;
    margin-left: 10px;
  }
  .FS__navbar-links_logo-return {
    font-size: 20px;
    margin-top: 5px;
    margin-left: 10px;
    cursor: pointer;
  }
}

@media screen and (max-width: 800px) /* Media query for larger mobile devices like ipad*/ {
  .FS__navbar {
    margin-top: 3rem;
    margin-left: 3rem;
    padding: 0rem ;
  }
  .FS__navbar-links_logo-fs {
    width: 40px;
    height: 40px;
    font-size: 12px;
  }
  .FS__navbar-links_logo-funnel-syndicate {
    font-size: 12px;
    margin-left: 2px;
  }
  .FS__navbar-links_logo-return {
    font-size: 8px;
    margin-top: 4px;
    margin-left: 2px;
    cursor: pointer;
  }
}

@media screen and (max-width: 550px) /*Media query for Mobile devices*/ {
  .FS__navbar {
    margin-top: 2rem;
    margin-left: 2rem;
    padding: 0rem ;
  }
  .FS__navbar-links_logo-fs {
    width: 40px;
    height: 40px;
    font-size: 12px;
  }
  .FS__navbar-links_logo-funnel-syndicate {
    font-size: 12px;
    margin-left: 2px;
  }
  .FS__navbar-links_logo-return {
    font-size: 8px;
    margin-top: 4px;
    margin-left: 2px;
    cursor: pointer;
  }
}
